// Fonts END

// Типографика

html,
body {
	font-family: 'Frederik', sans-serif;
	font-weight: normal;
	font-variant-numeric: normal;
	font-size: 32px;
	line-height: 1.5;
	color: $text-color;
	background: #fff;
	background-size: 1440px auto, 1440px auto;
	min-width: 375px;
	font-style: normal;
	font-feature-settings: 'pnum' on, 'lnum' on;

}

p {
	margin: 0 0 15px 0;
}

p:last-child {
	margin: 0;
}



a,
a:focus {
	color: $green-color;
	text-decoration: underline;
	transition: color .15s ease;
}

a:hover {
	text-decoration: none;
}

.green {
	color: $green-color;
}

h1,
.h1 {
	font-family: 'Frederik';
	font-size: 175px;
	line-height: 1;
	color: #fff;
	margin: 0 0 104px 0;
	font-weight: normal;
	white-space: nowrap;
}

h1 span,
.h1 span {
	font-weight: bold;
	color: $green-color;
}

h2,
.h2 {

}

h3,
.h3 {

}

.over-wrapper ul {
	list-style: none;
	margin: 0 0 15px 0;
	padding: 0;
}

.over-wrapper ul > li {
	position: relative;
}

.over-wrapper ol {
	list-style: none;
	margin: 0;
	padding: 0;
	counter-reset: item;
}

.over-wrapper ol > li {
	margin: 0 0 10px 0;
	padding: 0 0 0 30px;
	position: relative;
}

.over-wrapper ol > li:before {
	counter-increment: item;
	content: counter(item);
	position: absolute;
	left: 0;
	top: 0;
}




// Типографика END

// Buttons

.btn,
.btn:focus {
	-webkit-box-shadow: none;
	-moz-box-shadow: none;
	box-shadow: none;
	background-color: transparent;
	text-decoration: none !important;
	-webkit-border-radius: 0;
	-moz-border-radius: 0;
	border-radius: 0;
	vertical-align: top;
	border: none;
	position: relative;
	display: inline-block;
	-webkit-box-shadow: none;
	-moz-box-shadow: none;
	box-shadow: none;
	text-align: center;
	transition: background-color .2s ease, color .2s ease, box-shadow .2s ease, border-color .2s ease, box-shadow .2s ease, font-size .2s ease;
	-webkit-appearance: none !important;
}

.btn-1,
.btn-1:focus {
	position: relative;
	padding: 0;
	font-size: 16px;
	font-weight: 500;
	color: #000;
	background: #5FAF2D;
	padding: 7px 18px 9px 18px;
}



.btn-1.disabled,
.btn-1:disabled {
	background: #363636;
}

.btn-1-big,
.btn-1-big:focus {
	font-size: 18px;
	padding: 18px 30px 19px 30px;
	min-width: 240px;
}


// Buttons END

// Forms

.form-group {
	margin: 0 0 16px 0;
}

input[type=text],
input[type=email],
input[type=password],
textarea {
	border: none;
	padding: 21px 25px 19px 25px;
	background: rgba(31,31,31,.1);
	transition: all .2s ease;
	font-family: 'Frederik';
	font-size: 16px;
	line-height: 24px;
	font-weight: 600;
	color: #1F1F1F;
	width: 100%;
	display: block;
	outline: none !important;
	border-radius: 32px;
}

.form-agree {
	font-weight: 500;
	font-size: 14px;
	line-height: 20px;
	margin: 16px 0 0 0;
}

.form-agree a,
.form-agree a:focus {
	color: #1F1F1F;
	text-decoration: underline;
}

.form-agree a:hover {
	text-decoration: none;
}

// Forms END

// Sliders

.slick-arrow {
	position: absolute;
	z-index: 10;
	border: none;
	-webkit-appearance: none;
	width: 50px;
	height: 100px;
	background: #00D2FD;
	transition: all .3s ease;
	font-size: 0;
	line-height: 0;
	color: transparent;
	text-indent: -20000px;
	overflow: hidden;
	top: 50%;
	margin-top: -50px;
	cursor: pointer;
	outline: none !important;
}



.slick-arrow.slick-disabled {
	opacity: .2 !important;
}

.slick-arrow:before {
	content: "";
	width: 17px;
	height: 26px;
	background-repeat: no-repeat;
	background-position: 0 0;
	position: absolute;
	left: 50%;
	top: 50%;
	margin: -13px 0 0 0;
}

.slick-prev:before {
	left: 24px;
	background-image: url(../images/slick-prev.svg);
}

.slick-next:before {
	left: auto;
	right: 24px;
	background-image: url(../images/slick-next.svg);
}

.slick-prev {
	border-radius: 50px 0 0 50px;
	right: 15px;
}

.slick-next {
	border-radius: 0 50px 50px 0;
	right: -35px;
}

.slider-wrapper {
	margin: 0 -15px;
	position: relative;
}

.slick-slide {
	margin: 0 15px;
}

.slick-slide img {
	max-width: 100%;
}

.news-slider .slick-arrow {
	top: 160px;
}

.slider-countable-wrapper {
	padding-bottom: 65px;
}

.photo-slider-wrapper {
	margin-left: 0;
	margin-right: 0;
	margin-top: 36px;
	margin-bottom: 40px;
}

.photo-slider .slick-slide {
	margin: 0;
}

.slider-countable .slick-arrow {
	width: 45px;
	height: 45px;
	border-radius: 50%;
	top: auto;
	right: auto;
	bottom: -65px;
}

.slider-countable .slick-prev {
	left: 0;
}

.slider-countable .slick-next {
	left: 210px;
}

.slider-countable .slick-arrow:before {
	width: 11px;
	height: 16px;
	margin-top: -8px;
}

.slider-countable .slick-prev:before {
	left: 15px;
	background-image: url(../images/slick-prev-countable.svg);
}

.slider-countable .slick-next:before {
	right: 15px;
	background-image: url(../images/slick-next-countable.svg);
}

.slider-count {
	font-size: 20px;
	font-weight: 300;
	position: absolute;
	left: 0;
	bottom: 6px;
	text-align: center;
	width: 255px;
}

.slider-count-cur {
	font-weight: 800;
}

.slider-count-sep {
	margin: 0 5px;
}

// Sliders END

// Modals

.modal-backdrop {
	background: rgb(0, 1, 33);
}

.modal-backdrop.show {
	opacity: .8;
}

.modal-dialog {
	pointer-events: auto;
	width: auto;
	margin: 0 auto;
	display: flex;
	min-height: 100%;
	flex-direction: column;
	justify-content: center;
}

.form-modal .modal-dialog {
	max-width: 980px;
	padding: 30px;
}

.form-modal .modal-content {
	background: none;
	border: none;
	border-radius: 50px;
	position: relative;
}

.form-modal .modal-content:before {
	content: "";
	position: absolute;
	left: 20px;
	top: 20px;
	right: -20px;
	bottom: -20px;
	background: #00D2FD;
	border-radius: 50px;
}

.form-modal .modal-content-inner {
	position: relative;
	background: #fff;
	border-radius: 50px;
	padding: 76px 95px 80px 95px;
}

.form-modal .modal-header {
	display: block;
	border: none;
	padding: 0;
	margin: 0 0 32px 0;
}

.form-modal .modal-header h2,
.form-modal .modal-header .h2 {
	margin: 0;
}

.form-modal .modal-header-text {
	font-weight: bold;
	margin: 12px 0 0 0;
	max-width: 560px;
}

.form-modal .close {
	position: absolute;
	right: 30px;
	top: 30px;
	opacity: 1 !important;
	width: 65px;
	height: 65px;
	opacity: 1 !important;
	cursor: pointer;
	z-index: 10;
	border-radius: 50%;
	outline: none !important;
	transition: all .2s ease;
}

.form-modal .close:before {
	content: "";
	background: url(../images/close-modal.svg) 0 0 no-repeat;
	width: 27px;
	height: 27px;
	position: absolute;
	left: 19px;
	top: 19px;
	transition: all .2s ease;
}
.form-modal .close:after {
	content: "";
	background: url(../images/close-modal-hover.svg) 0 0 no-repeat;
	width: 27px;
	height: 27px;
	position: absolute;
	left: 19px;
	top: 19px;
	transition: all .2s ease;
	opacity: 0;
}



.form-modal .modal-body {
	padding: 0;
}

// Modals END

