	flex: 0 0 120px;
	max-width: 120px;
}

.footer-col-2 {
	flex-grow: 1;
}

.footer-links {
	display: flex;
	justify-content: flex-end;
	font-size: 16px;
	line-height: 1.5;
	margin: 0 -16px;
}

.footer-links-item {
	margin: 0 16px;
}

.footer-link,
.footer-link:focus {
	position: relative;
	padding: 0 0 0 36px;
	color: #fff;
	text-decoration: none !important;
	display: inline-block;
	transition: all .15s ease;
}



.footer-link-pic {
	width: 24px;
	height: 24px;
	display: flex;
	justify-content: center;
	align-items: center;
	position: absolute;
	left: 0;
	top: 0;
}

.footer-logo {
	margin: -6px 0 6px 0px;
}

.footer-logo img {
	display: block;
}

.footer-content {
	padding: 0 16px;
}

