	.btn-1:hover {
		background: #479327;
		color: #000;
	}

	.btn-1:active {
		background: #5FAF2D;
		color: #000;
	}

	ul.navbar-nav a:hover {
		color: #5FAF2D;
	}

	.footer-link:hover {
		color: #5FAF2D;
	}

}

