@media (max-width: 767px) {

	.tasks-list .col,
	.anim {
		opacity: 1 !important;
		transform: none !important;
	}

}

.section-top .anim,
.section-about .anim,
.section-stages .anim,
.section-prizes .anim,
.section-career .anim {
	opacity: 0;
	transform: translateY(50px);
}

.tasks-list .col {
	opacity: 0;
	transform: scale(.8);
}